<template>
  <div class="main" v-show="isMain">
    <div class="header" v-show="display==0">
      <h2><strong>upload</strong> de arquivos</h2>
    </div>    
    <div class="container2 glitch" v-show="display==0">
      <div class="border2"  @dragover="dragover" @dragleave="dragleave" @drop="drop">

        <transition name="uploadingtransition">
        <div v-show="uploadDisplay" class="uploading" @click="restart()">                    
          <span>Aguarde... </span>
          <progress id="progressBar" value="0" max="100"></progress> 
        </div>
        </transition>

        <transition name="prompttransition">
        <div v-show="promptDisplay" class="prompt" @click="restart()">
            <span>Upload <strong style="color: grey">concluido</strong>!</span>
            <br>
            Continuar? click.
        </div>
        </transition>
        
        <transition name="formtransition">                    
        <form v-show="formDisplay" class="form" enctype="multipart/form-data" method="post">           
          <img src="../assets/file_icon.png" alt="">
          <div>
            <h3>Arraste e solte seus <strong>arquivos</strong> aqui </h3>
            <span class="hr-line"><hr> ou <hr></span>
            <label id="file-label" for="file">Selecione</label>            
            <input id="file" type="file" name="filename" multiple>
          </div>
        </form>    
        </transition>                                   
  
      </div>
    </div>    

    <div v-show="isNetworkError" class="error">
      Network error. See console.
    </div>

                      <!--      LISTA COMEÇA AQUI        -->

    <p  v-if="display==0" style="margin: 10px 0 0px 0 ; font-size: .6em; font-style: italic  ">Limite máximo...</p>

    <!-- <div v-if="listDisplay" class="file-list">    

      <transition-group name="list" tag="ul" style="margin: 0; padding: 0; width: 100%">        
      <li class="image-item" v-for="(item) in filesList" :key="item.randomKey">       
        <div class="name-container">
          <div class="name-size">
              <span class="name">{{item.name}}</span>   
              <span class="size" :data="item.randomKey" >100 mb</span> 
          </div>
          <progress id="progressBar" :data="item.randomKey" value="0" max="100"></progress>                             
        </div>
      </li>
      </transition-group>

    </div>    -->
    <h3 v-if="false" id="upload-info">teste</h3>         
  </div>  
</template>

<script>
import axios from 'axios';
//import { uuid } from 'vue-uuid' // Import uuid  
export default {
  name: 'WFileUpload',
  data () {    
    return {        
      count: 0,    
      filesList: [],
      imagesList: [],
      showFileList: false,
      filesTotal:  null,
      filesResponseCount: 1,
      display: 0,
      formDisplay: true,
      promptDisplay: false,
      uploadDisplay: false,
      listDisplay: false,
      isNetworkError: false,
      isMain: true,


    }
  },
  created() {
    window.addEventListener('resize', this.onResize)
    window.addEventListener('load', this.onResize)
    var self = this
    //test if the server is online
    axios.get('http://localhost:3000/upload')
        .then(res => {
            res
        })
        .catch(err => { 
          console.log(err)
          self.isMain = false
          self.isNetworkError = true;  
          
          return
        })    
  },

  beforeDestroy() {
    window.removeEventListener('resize', this.onResize)
    window.removeEventListener('load', this.onResize)
  },  
  mounted() {    
    const fileInputElem = document.querySelector('#file');
    fileInputElem.addEventListener('change', this.handleFileChange)
  
  },
  methods: {
    onResize() {
      if (window.innerWidth > 960) {
        this.display = 0
      } else {
        this.display = 1
      }      
    },
    formatSizeUnits(bytes){
      // https://stackoverflow.com/questions/15900485/correct-way-to-convert-size-in-bytes-to-kb-mb-gb-in-javascript
      if      (bytes >= 1073741824) { bytes = (bytes / 1073741824).toFixed(2) + " GB"; }
      else if (bytes >= 1048576)    { bytes = (bytes / 1048576).toFixed(2) + " MB"; }
      else if (bytes >= 1024)       { bytes = (bytes / 1024).toFixed(2) + " KB"; }
      else if (bytes > 1)           { bytes = bytes + " bytes"; }
      else if (bytes == 1)          { bytes = bytes + " byte"; }
      else                          { bytes = "0 bytes"; }
      return bytes;
    },
    restart() {
      setTimeout(()=> this.promptDisplay = false, 600)      
      setTimeout(()=> this.formDisplay = true, 900)
      setTimeout(()=> this.filesList = [], 600)
      // const border = document.querySelector('.border2')
      // setTimeout( () =>   {
      //   border.style.border = "2px dashed rgb(129, 185, 190)"
        
      //  } ,1000)
      
    },
    uploadFiles(files) {
     
      
      let data = new FormData()
      files.forEach(file => {
        data.append('file', file)

      })


      const config = {
        onUploadProgress: function(progressEvent) {

          var percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total)          
          
          var progressBar = document.querySelector('#progressBar')          
          progressBar.value = percentCompleted     
                    
            // " bytes of " + progressEvent.total
          
          if (progressEvent.loaded == progressEvent.total) {
            progressBar.style.color = ''
          }
        }
      }      
      var self = this;
      axios.post('http://localhost:3000/upload', data, config)
        .then(res => {
          res

          setTimeout(()=> self.uploadDisplay = false, 400)
          setTimeout(()=> self.promptDisplay = true, 800)
        })
        .catch(err => console.log(err))
  },
  dragover(event) {
      event.preventDefault();
      // Add some visual fluff to show the user can drop its files
        if (!this.formDisplay) { return } 
        event.currentTarget.classList.add('is-over');      
    },
    dragleave(event) {
      // Clean up      
      if (!this.formDisplay) { return } 
      event.currentTarget.classList.remove('is-over');
    },
    drop(event) {
      event.preventDefault();

      if (!this.formDisplay) { return }

      // was drop, then start
      setTimeout( () => this.formDisplay = false, 1)
      setTimeout( ()=> this.uploadDisplay = true, 400 )
      // const border = document.querySelector('.border2')
      // setTimeout( () =>  {
      //   border.style.border = "2px dashed white"
        
      //  } ,200)

      var files = [...event.dataTransfer.files];
      setTimeout( ()=> this.uploadFiles(files), 600 )      
      event.currentTarget.classList.remove('is-over');   



    },
    handleFileChange(e) {     
      
      //var cloned = [...e.target.files];
      // was drop, then start
      setTimeout( () => this.formDisplay = false, 1)
      setTimeout( ()=> this.uploadDisplay = true, 400 )
      const container = document.querySelector('.container2')
      setTimeout( () =>  {
        //
        container
        
       } ,200)
      
      
      var files = [...e.target.files];
      setTimeout( ()=> this.uploadFiles(files), 600 )

      e.currentTarget.classList.remove('is-over');  
      e.target.value = '';    

    }
  }
}
</script>
<style scoped lang="scss">
  @import '@/css/transitions.scss';
  @import '@/css/multiupload.scss';
</style>
